* {
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
}

.container {
  max-width: 1260px;
  width: 100%;
}

@media #{$medium-and-up} {
  .container {
    width: 100%;
  }
}
@media #{$large-and-up} {
  .container {
    width: 100%;
  }
}

.container-wide {
  @extend .container;

  @media #{$medium-and-up} {
    width: 95%;
  }
  @media #{$large-and-up} {
    width: 90%;
  }
}

.content {
  position: relative;
  // padding-top: 200px;
  z-index: 3;
}

.flex {
  display: flex;

  @media #{$small-and-down} {
    flex-wrap: wrap;
  }
}

.flex-center {
  align-items: center;
}

.flex-end {
  justify-content: flex-end;
}

.flex-grow {
  flex: 1;
}

.flex-bottom {
  align-items: flex-end;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-grid {
  display: flex;
  flex-wrap: wrap;

  .flex-item {
    padding: 10px;
    // display: flex;
    position: relative;
  }

  @media #{$xsmall-and-down} {
    &.s1x {
      .flex-item {
        width: 100%;
      }
    }
  }
  @media #{$small-and-up} {
    &.m2x {
      .flex-item {
        width: 50%;
      }
    }

    &.m3x {
      .flex-item {
        width: 33.33%;
      }
    }
  }
  @media #{$medium-and-up} {
    &.l3x {
      .flex-item {
        width: 33.33%;
      }
    }

    &.l4x {
      .flex-item {
        width: 25%;
      }
    }
  }
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

.space-evenly {
  justify-content: space-evenly;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.float-left {
  @extend .left;
}

.float-center {
  margin-left: auto;
  margin-right: auto;
}

.absolute-right {
  position: absolute;
  right: 0;
}

.relative-right {
  position: relative;
  right: 0;
}

.relative {
  position: relative;
}

.mega-link.mega-link {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
}

@media #{$xsmall-and-down} {
  .row .col.xs12 {
    width: 100%;
  }
}

img {
  max-width: 100%;
  height: auto;
}

@mixin cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.border-top {
  border-top: 1px solid $color-grey-light;
  padding-top: 1em;
}

.page-footer {
  padding-top: 0;
}



